import React, { useEffect } from "react";
import Switch from "react-bootstrap/esm/Switch";
import { useDispatch, useSelector } from "react-redux";
import { Route } from "react-router-dom";
import { requestUserDetails } from "../../../redux/actions";
import Loader from "../../components/Common/Loader";
import Footer from "./Footer";
import Header from "./Header";
import VerifyUser from "../../containers/VerifyUser";
const HomepageContainer = React.lazy(() => import("./../../containers/Home"));
const PageDetailsContainer = React.lazy(() =>
  import("./../../containers/PostDetails")
);
const ProfileContainer = React.lazy(() => import("./../../containers/Profile"));
const MainLayout = () => {
  const dispatch = useDispatch();
  const { isLoading } = useSelector((state) => ({
    isLoading: state.authReducer.isLoading,
  }));
  useEffect(() => {
    window.scrollTo(0, 0);
    // eslint-disable-next-line
  }, [window.location.href]);
  useEffect(() => {
    dispatch(requestUserDetails());
    // eslint-disable-next-line
  }, []);
  return (
    <>
      <Header />
      <div className="main-block blog-page">
        <div className="container">
          {isLoading ? (
            <Loader />
          ) : (
            <React.Suspense fallback={<Loader />}>
              <Switch>
                <Route path="/" exact component={HomepageContainer} />
                {/* <Route path="/reset-password" exact component={ResetPasswordComponent} /> */}
                <Route path="/verify-user" exact component={VerifyUser} />
                <Route
                  path="/post/:id/:slug"
                  exact
                  component={PageDetailsContainer}
                />
                <Route
                  path="/user/:id/@:username"
                  exact
                  component={ProfileContainer}
                />
              </Switch>
            </React.Suspense>
          )}
          <Footer />
        </div>
      </div>
    </>
  );
};

export default MainLayout;
