import React from "react";
import { Modal } from "react-bootstrap";
import Loader from "../../Common/Loader";
const LoginFormComponent = React.lazy(() => import("./LoginForm"));
const LoginComponent = ({ handleClose, show, onSignUp,onFogotPassowrd }) => {
  return (
    <Modal
      show={show}
      onHide={handleClose}
      animation
      centered
      backdrop="static"
      className="auth-modal login-modal"
    >
      <Modal.Header closeButton></Modal.Header>
      <Modal.Body>
        <React.Suspense fallback={<Loader />}>
          <LoginFormComponent onSignUp={onSignUp}
          onFogotPassowrd={onFogotPassowrd} />
        </React.Suspense>
      </Modal.Body>
    </Modal>
  );
};

export default LoginComponent;
