import React from "react";
import { Modal } from "react-bootstrap";
import Loader from "../../Common/Loader";
const ForgotPasswordFormComponent = React.lazy(() => import("./ForgotPasswordForm"));

const ForgotComponent = ({ handleClose, show, onLogin }) => {
  return (
    <Modal
      show={show}
      onHide={handleClose}
      animation
      centered
      backdrop="static"
      className="auth-modal login-modal"
    >
      <Modal.Header closeButton></Modal.Header>
      <Modal.Body>
        <React.Suspense fallback={<Loader />}>
          <ForgotPasswordFormComponent onLogin={onLogin} />
        </React.Suspense>
      </Modal.Body>
    </Modal>
  );
};

export default ForgotComponent;
