import React, { useEffect, useState } from "react";
import { Nav, Navbar, Form, Button, FormControl } from "react-bootstrap";
import Skeleton from "react-loading-skeleton";
import { useDispatch, useSelector } from "react-redux";
import { Link, useRouteMatch, useHistory } from "react-router-dom";
import { setModalState, userDetailsSuccess } from "../../../redux/actions";
import { getFullName, getProfileURL } from "../../../utills/helpers";
import LoginComponent from "../../components/Auth/Login";
import SignupComponent from "../../components/Auth/Signup";
import ForgotComponent from "../../components/Auth/ForgotPassword";
import logo from "./../../assets/images/logo.png";
import { AuthInitialReducer } from "../../../redux/states";

const Header = () => {
  const location = useRouteMatch();
  const history = useHistory()
  const [selectedPage, setSelectedPage] = useState("");
  const { modalReducer, userDetails, isLoading } = useSelector((state) => ({
    modalReducer: state.modalReducer,
    userDetails: state.authReducer.details,
    isLoading: state.authReducer.isLoading,
  }));
  const {
    showLoginModal: showLogin,
    showRegisterModal: showSignUp,
    showForgotPasswordModal: showForgotPassword
  } = modalReducer;
  const dispatch = useDispatch();
  useEffect(() => {
    setSelectedPage("home");
  }, [location.path]);
  /**
   *
   */
  const openLoginModal = () => {
    dispatch(
      setModalState({
        showLoginModal: true,
        showRegisterModal: false,
        showForgotPasswordModal: false,
      })
    );
  };
  /**
   *
   */
  const closeLoginModal = () => {
    dispatch(
      setModalState({
        showLoginModal: false,
        showRegisterModal: false,
        showForgotPasswordModal: false,
      })
    );
  };
  /**
   *
   */
  const openSignupModal = () => {
    dispatch(
      setModalState({
        showLoginModal: false,
        showRegisterModal: true,
        showForgotPasswordModal: false,
      })
    );
  };
  /**
   *
   */
  const closeSignupModal = () => {
    dispatch(
      setModalState({
        showLoginModal: false,
        showRegisterModal: false,
        showForgotPasswordModal: false,
      })
    );
  };
  /**
 *
 */
  const openForgotPasswordModal = () => {
    dispatch(
      setModalState({
        showLoginModal: false,
        showRegisterModal: false,
        showForgotPasswordModal: true,
      })
    );
  };
  /**
   *
   */
  const closeForgotPasswordModal = () => {
    dispatch(
      setModalState({
        showLoginModal: false,
        showRegisterModal: false,
        showForgotPasswordModal: false,
      })
    );
  };
  //
  return (
    <>
      <header className={"header"}>
        <Navbar expand="lg" className="w-100">
          <Navbar.Brand as="div">
            <Link to={"/"}>
              <img src={logo} width={60} alt={"Main Logo"} />
            </Link>
          </Navbar.Brand>
          <Form inline className="header-search-form d-xl-block d-none">
            <FormControl type="text" placeholder="Search" />
            <Button variant="outline-success">
              <i className="fas fa-search"></i>
            </Button>
          </Form>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ml-auto">
              <Link
                to="/"
                className={`nav-link ${
                  selectedPage === "home" ? "active" : ""
                  }`}
              >
                Home
              </Link>
              {isLoading ? (
                <Link to="/" className={`nav-link`}>
                  <Skeleton width={200} />
                  &nbsp;
                </Link>
              ) : userDetails && userDetails._id && userDetails.userType !== "guest" ? (
                <>
                  <Link
                    to={getProfileURL(userDetails)}
                    className={`nav-link ${showLogin ? "active" : ""}`}
                  >
                    {getFullName(userDetails)}
                  </Link>
                  <Link
                    to={"/"}
                    className={`nav-link`}
                    onClick={(e) => {
                      e.preventDefault();
                      localStorage.removeItem("token");
                      localStorage.removeItem("tokenExpiry");
                      history.push("/")
                      dispatch(userDetailsSuccess(AuthInitialReducer.details));
                    }}
                  >
                    Logout
                  </Link>
                </>
              ) : (
                    <>
                      <Link
                        to="/"
                        className={`nav-link ${showLogin ? "active" : ""}`}
                        onClick={(e) => {
                          e.preventDefault();
                          openLoginModal();
                        }}
                      >
                        Login
                  </Link>
                      <Link
                        to="/"
                        className={`nav-link ${showSignUp ? "active" : ""}`}
                        onClick={(e) => {
                          e.preventDefault();
                          openSignupModal();
                        }}
                      >
                        Signup
                  </Link>
                    </>
                  )}
              <Form inline className="header-search-form d-xl-none">
                <FormControl type="text" placeholder="Search" />
                <Button variant="outline-success">
                  <i className="fas fa-search"></i>
                </Button>
              </Form>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </header>
      {userDetails && userDetails._id && userDetails.userType !== "guest" ? null : (
        <>
          <LoginComponent
            onSignUp={() => {
              openSignupModal();
            }}
            onFogotPassowrd={() => {
              openForgotPasswordModal();
            }}
            show={showLogin}
            handleClose={closeLoginModal}
          />
          <SignupComponent
            onLogin={() => {
              openLoginModal();
            }}
            show={showSignUp}
            handleClose={closeSignupModal}
          />
          <ForgotComponent
            onLogin={() => {
              openLoginModal();
            }}
            show={showForgotPassword}
            handleClose={closeForgotPasswordModal}
          />
        </>
      )}
    </>
  );
};

export default Header;
