import React, { useState } from "react";
import { Row, Col, Form, Button, InputGroup, FormControl, Spinner } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { getValidationErrors } from "../../../utills/helpers";
import { EmailAddressValidation } from "../../validations";
import { requestToSubscribe, setModalState } from "../../../redux/actions";
import { Link } from "react-router-dom";

const Footer = () => {
  const dispatch = useDispatch();
  const { isLoading } = useSelector(
    (state) => state.newsLetterReducer
  );
  const [email, setEmail] = useState("");
  const [errors, setErrors] = useState("");
  const handleChange = (event) => {
    const { target: { value } } = event;
    setEmail(value);
  }
  const handleSubmit = (e) => {
    e.preventDefault();
    try {
      EmailAddressValidation.validateSync({ email }, {
        abortEarly: false,
      });
      dispatch(requestToSubscribe({ email }));
    } catch (error) {
      setErrors(getValidationErrors(error));
    }
  };
  /**
  *
  */
  const openLoginModal = () => {
    dispatch(
      setModalState({
        showLoginModal: true,
        showRegisterModal: false,
        showForgotPasswordModal: false,
      })
    );
  };
  /**
   *
   */

  /**
 *
 */
  const openSignupModal = () => {
    dispatch(
      setModalState({
        showLoginModal: false,
        showRegisterModal: true,
        showForgotPasswordModal: false,
      })
    );
  };
  /**
   *
   */
  return <>
    <div className="footer">
      <Row>
        <Col md={4}>
          <h5>Learn More</h5>
          <ul className="footer-listing">
            <li><a href="javascript:void(0);">Home</a></li>
            <li><a href="javascript:void(0);">About</a></li>
            <li><a href="javascript:void(0);">Contact</a></li>
            <li><a href="javascript:void(0);">Blogs</a></li>
          </ul>
        </Col>
        <Col md={4}>
          <h5>Connect</h5>
          <ul className="footer-listing">
            <li><Link
              to="/"
              // className={`nav-link`}
              onClick={(e) => {
                e.preventDefault();
                openLoginModal();
              }}
            >Login</Link></li>
            <li> <Link
              to="/"
              onClick={(e) => {
                e.preventDefault();
                openSignupModal();
              }}
            >SignUp</Link></li>
          </ul>
        </Col>
        <Col md={4}>
          <div className="newslatter-wrap">
            <h5>Newsletter</h5>
            <p>Join us to get the latest news and updates.</p>
            <Form onSubmit={handleSubmit} className="mb-3">
              <InputGroup>
                <FormControl
                  placeholder="Enter Email Here...!"
                  aria-label=""
                  aria-describedby="basic-addon2"
                  name={"email"}
                  value={email}
                  onChange={handleChange}
                />
                <InputGroup.Append>
                  <Button variant="outline-secondary" type="submit"
                    disabled={isLoading}
                  >{isLoading ? (
                    <>
                      <Spinner />
                      &nbsp; Subscribe...
              </>
                  ) : (<>Subscribe</>)}</Button>
                </InputGroup.Append>
              </InputGroup>
              {errors.email ? (
                <p className="text-danger">{errors.email}</p>
              ) : null}
            </Form>
          </div>
        </Col>
        {/* <Col md={3}>
          <h5>Connect</h5>
          <ul className="footer-listing">
            <li><a href="javascript:void(0);">Home</a></li>
            <li><a href="javascript:void(0);">About</a></li>
            <li><a href="javascript:void(0);">Contact</a></li>
            <li><a href="javascript:void(0);">Blogs</a></li>
          </ul>
        </Col> */}
      </Row>
      <div className="copyright-section text-center">
        <p className="m-0">All Copyrights Reserevd by Writeearn | 2020.</p>
      </div>
    </div>
  </>;

};

export default Footer;
